/* Aesop-Inspired Input Component */
.aesop-input-group {
  position: relative;
  width: 100%;
  padding-top: 1rem;
  /* Space for floating label */
}

.aesop-input {
  width: 100%;
  background: transparent;
  border-bottom: 1px solid rgb(31 31 31 / 0.2);
  padding: 12px 0;
  font-family: 'Inter', sans-serif;
  color: #1F1F1F;
  transition: border-color 0.3s ease;
  outline: none;
  border-radius: 0;
  /* Strict square inputs */
}

.aesop-input:focus {
  border-bottom-color: transparent;
}

.aesop-input::-moz-placeholder {
  color: transparent;
  /* Hide default placeholder for floating label effect */
}

.aesop-input::placeholder {
  color: transparent;
  /* Hide default placeholder for floating label effect */
}

/* Floating Label */
.aesop-input-group label {
  position: absolute;
  top: 12px;
  /* Initial position matching padding */
  left: 0;
  font-size: 0.75rem;
  /* text-xs */
  font-family: 'Plus Jakarta Sans', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: rgb(31 31 31 / 0.4);
  pointer-events: none;
  transition: all 0.3s ease;
}

/* Float label on focus or when value exists (handled by JS or :not(:placeholder-shown)) */
.aesop-input:not(:-moz-placeholder)+label {
  top: -4px;
  font-size: 0.65rem;
  color: #1F1F1F;
  opacity: 0.7;
}
.aesop-input:focus+label,
.aesop-input:not(:placeholder-shown)+label {
  top: -4px;
  font-size: 0.65rem;
  color: #1F1F1F;
  opacity: 0.7;
}


/* Liquid Fill Accent */
.aesop-input-group::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 2px;
  /* Thicker accent */
  background-color: #C5A065;
  /* Restrained Bronze */
  transition: width 0.4s cubic-bezier(0.16, 1, 0.3, 1);
}

.aesop-input-group:focus-within::after {
  width: 100%;
}

/* Accessibility Focus Ring */
button:focus-visible,
a:focus-visible{
  outline-width: 2px;
  outline-offset: 4px;
  outline-color: #1F1F1F;
}

/* Gentle Error States */
.aesop-input.error {
  border-bottom-color: #BC6C25;
}

.aesop-error-msg {
  color: #BC6C25;
  font-family: 'Inter', sans-serif;
  font-size: 0.75rem;
  font-weight: 500;
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Glassmorphism Utility */
.bg-glass-matte {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0) 100%);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

/* 
========================================
   React Toastify Custom Overrides 
   (Japandi / Clinical Aesthetic)
========================================
*/

/* Container & Animation */
.Toastify__toast-container {
  padding: 1rem;
  width: auto;
  max-width: 400px;
}

.Toastify__toast {
  background-color: #FFFFFF !important;
  /* Pure White */
  color: #1F1F1F !important;
  font-family: 'Plus Jakarta Sans', sans-serif !important;
  font-size: 0.875rem !important;
  /* text-sm */
  line-height: 1.5 !important;
  font-weight: 500 !important;
  border-radius: 1.5rem !important;
  /* rounded-3xl */
  box-shadow: 0 10px 30px -10px rgba(0, 0, 0, 0.1) !important;
  /* shadow-soft */
  border: 1px solid rgba(0, 0, 0, 0.05) !important;
  padding: 1rem 1.5rem !important;
  margin-bottom: 1rem !important;
  display: flex !important;
  align-items: center !important;
  min-height: 64px !important;
}

/* Success Toast */
.Toastify__toast--success {
  border-left: 4px solid #606C38;
}

/* Error Toast */
.Toastify__toast--error {
  border-left: 4px solid #BC6C25;
}

/* Body Content */
.Toastify__toast-body {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

/* Progress Bar */
.Toastify__progress-bar {
  background: #1F1F1F;
  opacity: 0.1;
  height: 3px;
}

.Toastify__progress-bar--success {
  background: #606C38;
}

.Toastify__progress-bar--error {
  background: #BC6C25;
}

/* Close Button */
.Toastify__close-button {
  color: #1F1F1F;
  opacity: 0.3;
  margin-left: 1rem;
  transition: opacity 0.2s;
